import React from 'react';
import { useNavigate } from 'react-router-dom';
import { useApp } from '../../context/AppContext';
import { 
  Container, 
  Typography, 
  Paper, 
  Button, 
  Rating, 
  Box, 
  Table, 
  TableBody, 
  TableCell, 
  TableContainer, 
  TableHead, 
  TableRow,
  IconButton,
  Divider,
  Alert
} from '@mui/material';
import DeleteIcon from '@mui/icons-material/Delete';
import ShoppingCartIcon from '@mui/icons-material/ShoppingCart';
import CompareArrowsIcon from '@mui/icons-material/CompareArrows';
import './CompareProducts.css';

const CompareProducts: React.FC = () => {
  const { state, dispatch } = useApp();
  const navigate = useNavigate();

  const handleRemoveFromCompare = (productId: string) => {
    dispatch({ type: 'REMOVE_FROM_COMPARE', payload: productId });
  };

  const handleAddToCart = (product: any) => {
    dispatch({ type: 'ADD_TO_CART', payload: product });
  };

  const handleProductClick = (productId: string) => {
    navigate(`/product/${productId}`);
  };

  const handleClearCompare = () => {
    dispatch({ type: 'CLEAR_COMPARE' });
  };

  // Get all unique feature keys from all products
  const getAllFeatures = () => {
    const features = new Set<string>();
    
    state.compareList.forEach(product => {
      Object.keys(product).forEach(key => {
        // Skip these properties in the comparison table
        if (!['id', 'image', 'reviews', 'quantity'].includes(key)) {
          features.add(key);
        }
      });
    });
    
    return Array.from(features);
  };

  const formatFeatureName = (feature: string) => {
    return feature
      .replace(/([A-Z])/g, ' $1') // Add space before capital letters
      .replace(/^./, str => str.toUpperCase()) // Capitalize first letter
      .trim();
  };

  const renderFeatureValue = (product: any, feature: string) => {
    if (feature === 'price' || feature === 'originalPrice') {
      return product[feature] ? `$${product[feature].toFixed(2)}` : 'N/A';
    } else if (feature === 'rating') {
      return (
        <Box display="flex" alignItems="center">
          <Rating value={product[feature]} precision={0.5} size="small" readOnly />
          <Typography variant="body2" ml={1}>
            ({product[feature]})
          </Typography>
        </Box>
      );
    } else if (feature === 'inStock') {
      return product[feature] ? `${product[feature]} in stock` : 'Out of stock';
    } else if (feature === 'tags' && Array.isArray(product[feature])) {
      return product[feature].join(', ');
    } else if (feature === 'discount') {
      return product[feature] ? `${product[feature]}% off` : 'No discount';
    } else {
      return product[feature] || 'N/A';
    }
  };

  return (
    <Container className="compare-container">
      <Box className="compare-header">
        <Box display="flex" alignItems="center">
          <CompareArrowsIcon className="compare-icon" />
          <Typography variant="h4" component="h1" ml={1}>
            Compare Products
          </Typography>
        </Box>
        
        {state.compareList.length > 0 && (
          <Button 
            variant="outlined" 
            color="error" 
            startIcon={<DeleteIcon />}
            onClick={handleClearCompare}
          >
            Clear All
          </Button>
        )}
      </Box>

      {state.compareList.length === 0 ? (
        <Paper elevation={2} className="empty-compare">
          <CompareArrowsIcon className="empty-compare-icon" />
          <Typography variant="h6">No products to compare</Typography>
          <Typography variant="body1" color="textSecondary" sx={{ mb: 2 }}>
            Add products to compare their features side by side.
          </Typography>
          <Button 
            variant="contained" 
            color="primary" 
            onClick={() => navigate('/')}
          >
            Browse Products
          </Button>
        </Paper>
      ) : state.compareList.length === 1 ? (
        <Alert severity="info" sx={{ mb: 3 }}>
          Add at least one more product to compare features side by side.
        </Alert>
      ) : (
        <>
          <Box sx={{ display: 'grid', gridTemplateColumns: 'repeat(12, 1fr)', gap: 2 }} className="compare-products-grid">
            {state.compareList.map((product) => (
              <Box key={product.id} sx={{ gridColumn: { xs: 'span 12', sm: 'span 6', md: 'span 3' } }}>
                <Paper elevation={2} className="compare-product-card">
                  <Box className="compare-actions">
                    <IconButton 
                      size="small" 
                      color="error"
                      onClick={() => handleRemoveFromCompare(product.id)}
                    >
                      <DeleteIcon />
                    </IconButton>
                  </Box>
                  
                  <Box 
                    className="compare-image-container"
                    onClick={() => handleProductClick(product.id)}
                  >
                    <img 
                      src={product.image} 
                      alt={product.title} 
                      className="compare-product-image"
                    />
                  </Box>
                  
                  <Typography 
                    variant="h6" 
                    className="compare-product-title"
                    onClick={() => handleProductClick(product.id)}
                  >
                    {product.title}
                  </Typography>
                  
                  <Box display="flex" justifyContent="space-between" alignItems="center" mt={2}>
                    <Typography variant="h6" color="primary">
                      ${product.price.toFixed(2)}
                    </Typography>
                    
                    <Button
                      variant="contained"
                      color="primary"
                      size="small"
                      startIcon={<ShoppingCartIcon />}
                      onClick={() => handleAddToCart(product)}
                    >
                      Add to Cart
                    </Button>
                  </Box>
                </Paper>
              </Box>
            ))}
          </Box>
          
          <Divider sx={{ my: 4 }} />
          
          <Typography variant="h5" gutterBottom>
            Feature Comparison
          </Typography>
          
          <TableContainer component={Paper} className="compare-table-container">
            <Table className="compare-table">
              <TableHead>
                <TableRow>
                  <TableCell className="feature-name-cell">Feature</TableCell>
                  {state.compareList.map(product => (
                    <TableCell key={product.id} align="center">
                      {product.title.length > 30 
                        ? `${product.title.substring(0, 30)}...` 
                        : product.title}
                    </TableCell>
                  ))}
                </TableRow>
              </TableHead>
              <TableBody>
                {getAllFeatures().map(feature => (
                  <TableRow key={feature}>
                    <TableCell component="th" scope="row" className="feature-name-cell">
                      {formatFeatureName(feature)}
                    </TableCell>
                    {state.compareList.map(product => (
                      <TableCell key={`${product.id}-${feature}`} align="center">
                        {renderFeatureValue(product, feature)}
                      </TableCell>
                    ))}
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </>
      )}
    </Container>
  );
};

export default CompareProducts;