import React from 'react';
import { useNavigate } from 'react-router-dom';
import { useApp, Product as ProductType } from '../../context/AppContext';
import FavoriteBorderIcon from '@mui/icons-material/FavoriteBorder';
import FavoriteIcon from '@mui/icons-material/Favorite';
import './Product.css';

interface ProductProps {
  product: ProductType;
}

const Product: React.FC<ProductProps> = ({ product }) => {
  const { state, dispatch } = useApp();
  const navigate = useNavigate();

  const addToBasket = () => {
    dispatch({
      type: 'ADD_TO_CART',
      payload: product
    });
  };
  
  const isInWishlist = state.wishlist.some(item => item.id === product.id);
  
  const toggleWishlist = (e: React.MouseEvent) => {
    e.stopPropagation();
    if (isInWishlist) {
      dispatch({
        type: 'REMOVE_FROM_WISHLIST',
        payload: product.id
      });
    } else {
      dispatch({
        type: 'ADD_TO_WISHLIST',
        payload: product
      });
    }
  };
  
  const handleProductClick = () => {
    navigate(`/product/${product.id}`);
  };

  const renderStars = (rating: number) => {
    const stars = [];
    const fullStars = Math.floor(rating);
    const hasHalfStar = rating % 1 !== 0;
    
    for (let i = 0; i < fullStars; i++) {
      stars.push(<span key={i}></span>);
    }
    
    if (hasHalfStar) {
      stars.push(<span key="half"></span>);
    }
    
    const emptyStars = 5 - Math.ceil(rating);
    for (let i = 0; i < emptyStars; i++) {
      stars.push(<span key={`empty-${i}`} style={{ color: '#ddd' }}></span>);
    }
    
    return stars;
  };

  const getRandomReviewCount = () => {
    return Math.floor(Math.random() * 5000) + 100;
  };

  const isOnSale = Math.random() > 0.7;
  const originalPrice = isOnSale ? product.price * 1.2 : null;

  return (
    <div className="product">
      {isOnSale && <div className="product__badge">Deal</div>}
      
      <div className="product__wishlist" onClick={toggleWishlist}>
        {isInWishlist ? 
          <FavoriteIcon className="product__wishlist-icon active" /> : 
          <FavoriteBorderIcon className="product__wishlist-icon" />
        }
      </div>
      
      <img 
        src={product.image} 
        alt={product.title} 
        onClick={handleProductClick}
        className="product__image"
      />

      <div className="product__info" onClick={handleProductClick}>
        <p className="product__title">{product.title}</p>
        
        <div className="product__rating">
          <div className="product__stars">
            {renderStars(product.rating)}
          </div>
          <span className="product__ratingCount">
            ({getRandomReviewCount().toLocaleString()})
          </span>
        </div>

        <div className="product__price">
          <small>$</small>
          <strong>{product.price.toFixed(2)}</strong>
          {originalPrice && (
            <span style={{ 
              textDecoration: 'line-through', 
              color: '#565959', 
              fontSize: '14px',
              marginLeft: '8px'
            }}>
              ${originalPrice.toFixed(2)}
            </span>
          )}
        </div>

        <div className="product__shipping">FREE delivery</div>
        <div className="product__prime">Prime</div>
      </div>

      <div className="product__buttons">
        <button className="product__cart-button" onClick={addToBasket}>Add to Cart</button>
        <button 
          className="product__compare-button" 
          onClick={(e) => {
            e.stopPropagation();
            dispatch({ type: 'ADD_TO_COMPARE', payload: product });
          }}
        >
          Compare
        </button>
      </div>
    </div>
  );
};

export default Product;